.Modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color:$white;
    width: 800px;
    height: 100vh;
    margin-top: 94px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media all and (min-width:768px) {
        width: 100vw;
        margin-top: 105px;
    }
}

.Modal .Form__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $white;
    box-shadow: 5px 10px 13px $dark-blue;
    border-radius: 8px;
    height: 400px;
    width: 300px;
    margin-bottom: 94px;
   
    @media all and (min-width:768px) {
    width: 600px;
    height: 600px;
    border-radius: 15px;
    margin-bottom: 105px;
    }
}

.Modal .Add-books__form {
    margin: 0;
    display: flex;
    justify-content: center;

    @media all and (min-width:768px) {
        width: 500px;
    }
}

.Modal__close-button-container {
    align-self: flex-end;   
    margin-right: 5px;

    @media all and (min-width:768px) {
        width: 65px;
        margin-bottom: 40px;
    }
}

.Modal__close-button {
    outline: none;
    border: none;
}

.fas{
    font-size: 10px;

    @media all and (min-width:768px) {
        font-size: 15px;
    }
}

.Modal .Send-button__container {
    margin-right: 10px; 
    align-self: flex-end;  

    @media all and (min-width:768px) { 
        margin-right: 45px; 
    }
}






    